body {
  font-family: "Roboto Condensed", "Arial", sans-serif;
  color: #1B2D37;
  position: relative;
  background-color: #ffffff; }

img {
  width: 100%;
  height: auto; }

.visually-hidden {
  position: absolute;
  clip: rect(0 0 0 0);
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  overflow: hidden; }

.page-header {
  background-image: url("../../img/background-desktop@1x.png");
  background-color: #1B2D37;
  background-position: center bottom;
  background-repeat: no-repeat;
  background-size: cover;
  min-height: 878px; }
  @media (min-resolution: 144dpi), (min-resolution: 1.5dppx) {
    .page-header {
      background-image: url("../../img/background-desktop@2x.png"); } }

.page-header__wrapper-alignment {
