  font-weight: 700; }

.page-header__text {
  color: #ffffff;
  font-size: 22px;
  line-height: 35px;
  font-family: 'PT Mono';
  font-weight: 400;
  position: relative;
  padding-left: 98px;
  margin-bottom: 60px; }
  .page-header__text::before {
    content: '';
