    height: 2px;
    background-color: #ffffff;
    position: relative;
    left: -93px;
    top: 19px; }

.page-header__button {
  font-weight: 700;
  text-decoration: none;
  color: #ffffff;
  font-size: 16px;
  line-height: 19px;
  text-transform: uppercase;
  letter-spacing: 0.8px;
  background-color: #00B2FF;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  min-width: 181px;
  padding: 18px 45px;
  margin-left: 98px; }
  .page-header__button:hover {
