  .page-header__button:focus {
    background-color: rgba(0, 178, 255, 0.5); }
  .page-header__button:active {
    background-color: rgba(0, 178, 255, 0.7); }

.page-header__link {
  display: block;
  width: 100px;
  position: relative;
  writing-mode: tb-rl;
  transform: rotate(180deg);
  text-decoration: none;
  color: #ffffff;
  font-size: 14px;
  line-height: 16px;
  font-family: 'PT Mono';
  font-weight: 400;
  margin-top: 111px;
  margin-left: 5px; }
  .page-header__link:hover {
    color: rgba(0, 178, 255, 0.5); }
